@import './_variables.scss';
@import './utilities.scss';
@import './navbar.scss';
@import './auth.scss';
@import './dashboard.scss';
@import './jewelry.scss';

/* ensure predictable sizing for all form controls */
*,
*::before,
*::after {
    box-sizing: border-box;
}

input,
textarea,
select,
button {
    box-sizing: border-box;
}

body {
    font-family: Inter, Arial, sans-serif;
    margin: 0;
    padding: 0;
    background: $bg;
    color: $text;
}

.container {
    max-width: 1100px;
    margin: 0 auto;
    padding: 1.5rem;
}