@import './_variables.scss';

.dashboard {
    padding: 1.5rem 0
}

.hero {
    display: flex;
    justify-content: space-between;
    gap: 1rem;
    align-items: center;
    margin-bottom: 1rem
}

.cards {
    display: flex;
    gap: 1rem
}

.card {
    background: $card-bg;
    padding: 1rem 1.25rem;
    border-radius: 10px;
    box-shadow: 0 6px 18px $shadow-2;
    flex: 1
}

.card h4 {
    margin: 0;
    color: $muted;
    font-size: .9rem
}

.card .value {
    font-size: 1.6rem;
    font-weight: 700;
    margin-top: .4rem
}

.grid {
    display: grid;
    grid-template-columns: 1fr minmax(260px, 380px);
    gap: 1rem
}

.panel {
    background: $card-bg;
    padding: 1rem;
    border-radius: 10px;
    box-shadow: 0 6px 18px $shadow-2
}

.transactions-list ul {
    list-style: none;
    padding: 0;
    margin: 0
}

.transactions-list li {
    display: flex;
    justify-content: space-between;
    padding: .6rem 0;
    border-bottom: 1px dashed $border-muted
}

@media (max-width:900px) {
    .grid {
        grid-template-columns: 1fr
    }

    .hero {
        flex-direction: column;
        align-items: flex-start
    }

    .cards {
        width: 100%
    }
}

/* small screen safety: keep page padded on very small devices */
@media (max-width:420px) {
    .container {
        padding-left: 1rem;
        padding-right: 1rem
    }
}